<template>
  <div class="test">
    <el-card class="login-container box-card">
      <el-form :model="loginForm" status-icon :rules="loginRules" ref="loginForm" label-width="auto">
        <h3 class="login_title">系统登录</h3>
        <el-form-item label="用户名" prop="username">
          <el-input type="text" v-model="loginForm.username" placeholder="请输入账号"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="loginForm.password" placeholder="请输入密码"></el-input>
        </el-form-item>

        <el-button style="margin-top: 10px" type="primary" @click="submitLoginForm('loginForm')">登录</el-button>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { getMenu } from "@/api";
import Cookie from "js-cookie";

export default {
  data() {
    return {
      loginForm: {
        username: "",
        password: "",
      },
      loginRules: {
        username: [{ required: true, message: "请输入账号", trigger: "blur" }],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    submitLoginForm() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          getMenu(this.loginForm).then((data) => {
            if (data.code == 20000) {
              Cookie.set("token", data.data.token);
              this.$store.dispatch("setMenu", data.data.menu);
              this.$store.dispatch("registerRouter", this.$router);
              this.$message.success("登陆成功");
              this.$router.replace("/home");
              this.$notify({
                title: "欢迎登录",
                message: this.$createElement(
                  "b",
                  { style: "color: teal" },
                  `欢迎${this.loginForm.username}登录`
                ),
                position: "bottom-right",
              });
            } else {
              this.$message.error("账号或密码错误!");
              this.resetForm("loginForm");
            }
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style lang="less" scoped>
.test {
  width: 100vw;
  height: 100vh;
  background-image: url("@/assets/images/Lucy.jpg");
  background-size: cover;
  position: relative;
}

.login-container {
  position: absolute;
  left: 50%;
  top: 22%;
  transform: translateX(-50%);
  width: 20%;
  border-radius: 10px;
  box-shadow: 0 0 25px #cac6c6;
}

.el-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;

  h3 {
    text-align: center;
    margin-bottom: 30px;
    letter-spacing: 5px;
    color: #505458;
  }
}
</style>